define('portia-web/helpers/trim', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports.trim = trim;

  function trim(input, length) {
    if (!input) {
      return '';
    }
    return input.substring(0, length || 45);
  }

  exports['default'] = Ember['default'].Handlebars.makeBoundHelper(trim);

});